import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-input-box',
  templateUrl: './input-box.component.html',
  styleUrls: ['./input-box.component.css'],
})

export class InputBoxComponent implements OnInit {
  selectForm: FormGroup;

  @Output() messageEvent = new EventEmitter<Object>(); /* Permet de créer un évènement détectable par le composant parent */
  
  ngOnInit() {
    this.selectForm = new FormGroup({
      latitude: new FormControl(null, [Validators.required]),
      longitude: new FormControl(null, [Validators.required]),
      date: new FormControl(),
    });
  }

  confirm() {  /* Permet de confirmer la validité des informations transmises par le formulaire lorsque l'on appuie sur le bouton d'envoi */
    console.log('Données du formulaire...', this.selectForm);
    if (this.selectForm.valid) {
      this.send();
      this.selectForm = new FormGroup({
        latitude: new FormControl(null, [Validators.required]),
        longitude: new FormControl(null, [Validators.required]),
        date: new FormControl(),
      });
    }
    else {
    }
  }

  send() {  /* Sert à rendre les informations du formulaire disponibles à son composant parent */
    this.messageEvent.emit(this.selectForm.value);
    console.log('Formulaire envoyé');
  }

}