import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-display-box',
  templateUrl: './display-box.component.html',
  styleUrls: ['./display-box.component.css']
})
export class DisplayBoxComponent implements OnInit {
  values = {
    sunrise : "",
    solar_noon : "",
    sunset : "",
    day_length : "",
    civil_twilight_begin : "",
    civil_twilight_end : "",
    nautical_twilight_begin : "",
    nautical_twilight_end : "",
    astronomical_twilight_begin : "",
    astronomical_twilight_end : ""
  };
  message = {
    latitude : "",
    longitude : "",
    date : "" ,};
  path = "";  

  constructor(
    private http: HttpClient) { }

  ngOnInit() {}

  receiveData($event) { /* récupère les données envoyées par le composant enfant suite à la détection de l'envoi d'un formulaire valide */
    this.message = $event;
    this.fetchTimes();
  }

  private fetchTimes() {  /* Permet de récupérer les informations fournies par l'API en fonction des données récupérées à partir du formulaire */
    if (this.message.date == null) {
      this.path = 'https://api.sunrise-sunset.org/json?lat=' + this.message.latitude + '&lng=' + this.message.longitude;
    }
    else {
      this.path = 'https://api.sunrise-sunset.org/json?lat=' + this.message.latitude + '&lng=' + this.message.longitude + '&date=' + this.message.date;
    }
    this.http.get(this.path)
      .pipe(map(responseData => {
        const val = [];
        for (const key in responseData) {
          if (responseData.hasOwnProperty(key)) {
            val.push({...responseData[key], id: key})
          }
        }
        return val;
      })
    )
    .subscribe(posts => this.values = posts[0]);
  }
}